@keyframes logout{
    from{
        transform: rotate(10deg);
    }
    to{
        transform: rotate(-10deg);
    }
}
.logout{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.logout svg{
    font-size: 10vh;
    color: #3f51b5;
    animation: logout 1s  alternate linear infinite ;
}